.footer {
    background-color: #f8f8f8;
    border-top: 1px solid #ddd;
    font-size: 14px;
    padding: 20px;
}

.footer-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-left {
    display: flex;
    align-items: center;
}

.footer-right {
    display: flex;
    align-items: center;
}

.footer-link {
    margin-left: 20px;
    text-decoration: none;
    color: black;
}

.footer-link:hover {
    text-decoration: underline;
}

.footer-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    font-weight: bold;
}
