.clothing-collection {
    display: flex;
    justify-content: space-around;
    flex-wrap: nowrap; /* 아이템들이 가로로 나열되도록 설정 */
    overflow-x: auto; /* 가로 스크롤 가능하도록 설정 */
}

.clothing-item {
    margin: 10px;
    text-align: center;
    flex: 0 0 auto; /* 아이템들이 가로로 나열되도록 설정 */
}

.clothing-item img {
    max-width: 200px; /* 이미지의 최대 너비 설정 */
    height: auto;
}
